@import '../../style/_vars.scss';

//---------HEADER----------
.header {
    padding: 50px 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-size: 28px;
}

.links {
    font-size: 0.85rem;

    &__link {
        color: #000;
        &_active {
            color: $main-color;
        }
    }
}
@media (max-width: 500px) {
    .header {
        flex-direction: column;
        gap: 10px;
        padding: 25px 5px;
    }
}