@import '../../style/vars';

.store__item {
    width: 225px;
    height: 410px;
    transform: translateY(0);
    transition: all 0.3s ease;

    &:hover {
        cursor: pointer;
        transform: translateY(-10px);           

        img {
            box-shadow: (0px 5px 20px $main-color);
        }
    }
f
    & .img {
        width: 225px;
        // height: 345px;
        img {
            // width: 100%;
            // height: 100%;
            object-fit: cover;
        }
    }
    & .info {
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        .title, .price {
            padding: 5px;
            font-size: 0.5rem;
        }

        .price {
            font-weight: normal;
        }
    }
}