@import 'vars.scss';

* {
    padding: 0;
    margin: 0;
    border: 0;
}
  
*, *:before, *:after {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
  
:focus, :active {
    outline: none;
}
  
a:focus, a:active {
    outline: none;
}
  
nav, footer, header, aside {
    display: block;
}
  
html, body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 28px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
  
input, button, textarea {
    font-family: inherit;
}
  
input::-ms-clear {
    display: none;
}
  
button {
    cursor: pointer;
}
  
button::-moz-focus-inner {
    padding: 0;
    border: 0;
}
  
ul, li {
    list-style: none;
}
  
a, a:visited {
    text-decoration: none;
}
  
a:hover {
    text-decoration: none;
}
  
img {
    vertical-align: top;
}

html, body{
    font-family: 'Roboto Condensed';
    font-size: 28px;
    font-weight: 700;
}

a {
    color: #fff;
}

.bg__art {
    width: 100%;
    background-repeat: no-repeat;
    background-position: bottom right 5%;

    @media (max-width: 1100px) {
        background-position: bottom right;
        background-size:auto;
    }

    @media (max-width: 800px) {
        background-image: none;
    }
}

.container {
    max-width: 1100px;
    margin: 0 auto;
    font-weight: 700;
    
}

.accent {
    color: $main-color;
}

button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 40px;
    clip-path: polygon(15% 0, 100% 0, 100% 85%, 85% 100%, 0 100%, 0% 60%, 0 15%);    
    // clip-path: polygon(9% 0, 100% 0, 100% 91%, 91% 100%, 0 100%, 0 8%);

    transition: transform 0.3s ease;

    &:hover, &:focus {
        transform: translateY(-5px);
    }
    
    & a {
        font-size: 0.5rem;
        text-transform: uppercase;
        color: #fff;
    }
}

.char-name {
    text-transform: uppercase;
    font-size: 0.78rem;

    &_white {
        color: #fff;
    }
}

.homepage {
    position: relative;
    background-color: $main-color;
    color: #fff;
    text-transform: uppercase;
}

.wiki {
    background-color: $sec-color;
    color: #fff;
    text-transform: uppercase;

}

.main {
    padding-top: 50px;
    display: flex;
    gap: 25px;

    @media (max-width: 800px) {
        flex-direction: column;
    }
}

.active {
    box-shadow: (0px 5px 20px $main-color);
    transform: translateY(-8px);
}